/* page.module.css */

.container {
    max-width: 1200px;
    margin: 0 auto;
    padding: 0 15px;
  }
  
  .row {
    display: flex;
    flex-wrap: wrap;
    margin: 0 -12px;
  }
  
  .g-4 {
    gap: 16px;
  }
  
  .col-md-8, .col-lg-6 {
    flex: 0 0 auto;
    padding: 0 12px;
  }
  
  .col-md-8 {
    width: 66.66666667%;
  }
  
  .col-lg-6 {
    width: 50%;
  }
  
  @media (max-width: 991.98px) {
    .col-lg-6 {
      width: 100%;
    }
  }
  
  @media (max-width: 767.98px) {
    .col-md-8 {
      width: 100%;
    }
  }
  
  .vstack {
    display: flex;
    flex-direction: column;
  }
  
  .gap-4 {
    gap: 16px;
  }
  
  .card {
    position: relative;
    display: flex;
    flex-direction: column;
    min-width: 0;
    word-wrap: break-word;
    background-color: #fff;
    background-clip: border-box;
    border: 1px solid rgba(0, 0, 0, 0.125);
    border-radius: 0.5rem;
    box-shadow: 0 0.125rem 0.25rem rgba(0, 0, 0, 0.075);
  }
  
  .card-header {
    padding: 1rem 1.25rem;
    margin-bottom: 0;
    background-color: rgba(0, 0, 0, 0.03);
    border-bottom: 1px solid rgba(0, 0, 0, 0.125);
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  
  .card-header:first-child {
    border-radius: calc(0.5rem - 1px) calc(0.5rem - 1px) 0 0;
  }
  
  .card-title {
    margin-bottom: 0;
    font-size: 1.25rem;
    font-weight: 500;
  }
  
  .card-body {
    flex: 1 1 auto;
    padding: 1.25rem;
  }
  
  .border-0 {
    border: 0 !important;
  }
  
  .pb-0 {
    padding-bottom: 0 !important;
  }
  
  .d-sm-flex {
    display: flex !important;
  }
  
  .text-center {
    text-align: center !important;
  }
  
  .align-items-center {
    align-items: center !important;
  }
  
  .justify-content-between {
    justify-content: space-between !important;
  }
  
  .justify-content-center {
    justify-content: center !important;
  }
  
  .justify-content-md-start {
    justify-content: flex-start !important;
  }
  
  /* Button styles */
  .btn {
    display: inline-flex;
    align-items: center;
    font-weight: 400;
    text-align: center;
    white-space: nowrap;
    vertical-align: middle;
    user-select: none;
    border: 1px solid transparent;
    padding: 0.375rem 0.75rem;
    font-size: 0.875rem;
    line-height: 1.5;
    border-radius: 0.25rem;
    transition: color 0.15s ease-in-out, background-color 0.15s ease-in-out, 
                border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
  }
  
  .btn-primary {
    color: #fff;
    background-color: #0d6efd;
    border-color: #0d6efd;
  }
  
  .btn-primary:hover {
    color: #fff;
    background-color: #0b5ed7;
    border-color: #0a58ca;
  }
  
  .btn-primary-soft {
    color: #0d6efd;
    background-color: rgba(13, 110, 253, 0.1);
    border-color: transparent;
  }
  
  .btn-primary-soft:hover {
    color: #fff;
    background-color: #0d6efd;
  }
  
  /* Icon styles */
  .icon-md {
    width: 2rem;
    height: 2rem;
    display: inline-flex;
    align-items: center;
    justify-content: center;
  }
  
  /* Tabs styles */
  .nav-tabs {
    border-bottom: 1px solid #dee2e6;
    display: flex;
    flex-wrap: wrap;
    padding-left: 0;
    margin-bottom: 1rem;
    list-style: none;
  }
  
  .nav-tabs .nav-item {
    margin-bottom: -1px;
  }
  
  .nav-tabs .nav-link {
    border: 1px solid transparent;
    border-top-left-radius: 0.25rem;
    border-top-right-radius: 0.25rem;
    padding: 0.5rem 1rem;
    color: #0d6efd;
    text-decoration: none;
    transition: color 0.15s ease-in-out, background-color 0.15s ease-in-out, 
                border-color 0.15s ease-in-out;
  }
  
  .nav-tabs .nav-link:hover {
    border-color: #e9ecef #e9ecef #dee2e6;
    isolation: isolate;
  }
  
  .nav-tabs .nav-link.active {
    color: #495057;
    background-color: #fff;
    border-color: #dee2e6 #dee2e6 #fff;
  }
  
  /* Tab content */
  .tab-content {
    margin-top: 1rem;
  }
  
  .tab-content > .tab-pane {
    display: none;
  }
  
  .tab-content > .active {
    display: block;
  }
  
  /* Grid for photos */
  .row.g-3 {
    display: flex;
    flex-wrap: wrap;
    margin: -0.5rem;
  }
  
  .row.g-3 > [class*="col-"] {
    padding: 0.5rem;
  }
  
  .col-6 {
    flex: 0 0 50%;
    max-width: 50%;
  }
  
  .col-lg-3 {
    flex: 0 0 25%;
    max-width: 25%;
  }
  
  @media (max-width: 991.98px) {
    .col-lg-3 {
      flex: 0 0 50%;
      max-width: 50%;
    }
  }
  
  @media (max-width: 575.98px) {
    .col-6 {
      flex: 0 0 100%;
      max-width: 100%;
    }
  }
  
  /* Photo item styles */
  .position-relative {
    position: relative;
  }
  
  .position-absolute {
    position: absolute;
  }
  
  .bottom-0 {
    bottom: 0;
  }
  
  .end-0 {
    right: 0;
  }
  
  .rounded {
    border-radius: 0.5rem !important;
  }
  
  .img-fluid {
    max-width: 100%;
    height: auto;
  }
  
  /* Dropdown styles */
  .dropdown {
    position: relative;
  }
  
  .dropdown-menu {
    position: absolute;
    z-index: 1000;
    display: none;
    min-width: 10rem;
    padding: 0.5rem 0;
    margin: 0;
    font-size: 0.875rem;
    color: #212529;
    text-align: left;
    list-style: none;
    background-color: #fff;
    background-clip: padding-box;
    border: 1px solid rgba(0, 0, 0, 0.15);
    border-radius: 0.25rem;
    right: 0;
  }
  
  .dropdown-menu-end {
    right: 0;
    left: auto;
  }
  
  .dropdown-item {
    display: block;
    width: 100%;
    padding: 0.25rem 1rem;
    clear: both;
    font-weight: 400;
    color: #212529;
    text-align: inherit;
    text-decoration: none;
    white-space: nowrap;
    background-color: transparent;
    border: 0;
  }
  
  .dropdown-item:hover {
    color: #1e2125;
    background-color: #e9ecef;
  }
  
  .dropdown-divider {
    height: 0;
    margin: 0.5rem 0;
    overflow: hidden;
    border-top: 1px solid rgba(0, 0, 0, 0.15);
  }
  
  /* Add photo box */
  .border-dashed {
    border-style: dashed !important;
  }
  
  .border-2 {
    border-width: 2px !important;
  }
  
  .text-center {
    text-align: center !important;
  }
  
  .d-flex {
    display: flex !important;
  }
  
  .align-items-center {
    align-items: center !important;
  }
  
  .justify-content-center {
    justify-content: center !important;
  }
  
  .stretched-link::after {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 1;
    content: "";
  }
  
  .fs-1 {
    font-size: 2.5rem !important;
  }
  
  .mt-2 {
    margin-top: 0.5rem !important;
  }
  
  /* Responsive adjustments */
  @media (max-width: 767.98px) {
    .card-header {
      flex-direction: column;
      text-align: center;
      gap: 0.5rem;
    }
    
    .nav-tabs {
      overflow-x: auto;
      flex-wrap: nowrap;
      white-space: nowrap;
    }
  }